body{
  font-family: 'Roboto', sans-serif;
}
#main{
  padding: 0 !important;
}
.fusion-header{
  background: $color-header !important;
  padding: 0;
}
.fusion-header-wrapper{
  background: $color-header;
  color: $color-header-menu;
  border-top: 2px solid darken($color-pr, 5%);
}
.header-container{
  & > .wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: nowrap;
    align-items: flex-start;
    justify-content: space-between;

    & > .site-navs{
      .holder{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        flex-wrap: nowrap;
        align-items: flex-start;
        height: $header-height;
        line-height: $header-height;
        & > .site {
          height: 100%;
          padding: 0 1px;
          a {
            display: block;
            height: 100%;
            overflow: hidden;
            padding: 0 10px;
            font-size: 0.85em;
            color: $color-header-site-text;
            background: lighten($color-header, 15%);
            border-top: 2px solid $color-pr;
            margin-top: -2px;

            a:hover{
              color: white;
            }
          }

          &.current-site{
            a {
              background: none;
            }
          }

          &.site-gastro{
            a{
              border-top: 2px solid $color-site-gastro;
            }
          }
          &.site-consulting{
            a{
              border-top: 2px solid $color-site-consulting;
            }
          }
          &.site-sport{
            a{
              border-top: 2px solid $color-site-sport;
            }
          }
        }
      }
      a.logo {
        img {
          max-height: 40px;
          height: 40px;
          vertical-align: middle;
          margin-top: -4px;
        }
      }
    }

    & > .menu{
      flex: 1;
    }

    & > .social{
      flex-basis: 200px;
      text-align: right;
      line-height: $header-height;
      padding: 0 15px 0 0;
      .fusion-social-networks-wrapper{
        margin: 0;
        & > a {
          background: $color-pr;
          color: white !important;
          padding: 5px;
          font-size: 12px;
          margin: 0 5px;
          border-radius: 30px;
        }
      }
    }
  }

  .menu {
    height: $header-height;
    .menu-fomenu-container{
      height: 100%;
    }
    a, a:visited, a:hover{
      color: black;
      font-weight: 300;

      i {
        color: $color-pr;
      }
    }
    ul, li {
      list-style: none;
      padding: 0;
      margin: 0;
    }

    ul.menu{
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
      height: 100%;

      & > li {
        border-right: 0.5px solid lighten($color-header, 10%);
        height: 100%;
        position: relative;
        -webkit-transition: all .4s;
        transition: all .4s;

        &:hover,
        &.current-menu-item{
          background: #ffffff;
        }

        & > a{
          display: block;
          padding: 0 20px;
          height: 100%;
          line-height: 1;
          font-weight: 300 !important;

          .ico {
            text-align: center;
            margin: 12px 0 5px 0;
            i {
              font-size: 15px;
            }
          }
        }

        & > .sub-menu{
          position: absolute;
          left: 0;
          min-width: 200px;
          max-width: 320px;
          top: $header-height;
          border: 0.5px solid #f1f1f1;
          border-top: none;

          & > li {
            border-bottom: 0.5px solid #f1f1f1;
            & > a {
              background: #ffffff;
              padding: 5px 10px;
              display: block;
              font-size: 0.8em;
              width: 100%;
              -webkit-transition: all .4s;
              transition: all .4s;

              &:hover{
                background: #f1f1f1;
                box-shadow: inset 4px 0 0 $color-pr;
                color: black;
              }
            }
          }
        }

        &:hover{
          & > .sub-menu{
            display: block;
          }
        }
      }
    }

    ul.sub-menu {
      display: none;
    }
  }
}

.szolgaltatas-list-holder{
  .list{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    margin: -10px;

    & > .item{
      flex-basis: calc(100% / 3);
      padding: 10px;
      margin-top: -45px;

      & > .wrapper{
        background: white;

        .image{
          height: 16vh;
          & > .wrapper{
            height: 100%;
            position: relative;
            overflow: hidden;

            img {
              position: absolute;
              height: 110%;
              max-height: 110%;
              width: auto;
              max-width: initial;
              top: 50%;
              left: 50%;
              -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
            }
          }
        }

        .datas{
          .title{
            margin: 10px 0 15px 0;
            text-transform: uppercase;
            font-weight: 300;
            line-height: 1.1;
            text-align: center;
            height: 50px;
            overflow: hidden;
            padding: 10px;

            a{
              color: #273034;
              font-size: 1.4em;
            }
          }

          .desc{
            height: 80px;
            overflow: hidden;
            text-align: center;
            color: #5a6d75;
            line-height: 1.3;
            padding: 0 10px;
          }
        }

        .button {
          a{
            text-align: center;
            display: block;
            padding: 10px;
            font-size: 1.6em;
            font-weight: bold;
            text-transform: uppercase;
            background: #8d9fa6;
            color: white;
            -webkit-transition: all .4s;
            transition: all .4s;

            &:hover{
              background: $color-pr;
            }
          }
        }
      }
      &.i2,
      &.i5{
        margin-top: 5px;
      }

      &.fake-item{
        a {
          display: block;
          background: $color-pr;
          color: white;
          -webkit-transition: all .4s;
          transition: all .4s;

          .text{
            text-align: center;
            line-height: 1.6;
            font-weight: bold;
            font-size: 3em;
            padding: 42px 0;
            text-transform: uppercase;
          }

          .ico {
            text-align: center;
            background: darken($color-pr, 5%);
            padding: 50px 25px;
            -webkit-transition: all .4s;
            transition: all .4s;
            i{
              font-size: 50px;
            }
          }
        }
        &:hover{
          a{
            background: darken($color-pr, 5%);
            .ico {
              background: darken($color-pr, 10%);
            }
          }
        }
      }
    }
  }
}

.pagination{
  text-align: center;
  margin: 10px auto;
  width: 100%;
  & > ul {
    list-style: none;
    li {
      list-style: none;
      display: inline-block;
      *display: inline;
      zoom: 1;
      padding: 2px;

      a{
        display: block;
        padding: 0 10px;
        text-align: center;
        line-height: 30px;
        background: #ffffff;
        color: black;
        border: 0.5px solid #d8d8d8;
        border-radius: 0;
        text-decoration: none;
      }

      &:hover{
        a {
          color: white;
          background: $color-sec;
        }
      }
      &.active{
        a{
          background: $color-pr;
          color: white;
        }
      }
    }
  }
}

.fusion-main-menu > a,
.fusion-main-menu .menu-item > a{
  text-transform: uppercase;
