.fusion-modal h1, .fusion-page-title-bar h1, .fusion-widget-area h1, .post-content h1{
  font-size: 2.2em;
  color: black;
  font-weight: bold;
  position: relative;

  &:after{
    content:"";
    height: 5px;
    background: $color-pr;
    width: 40%;
    position: absolute;
    left: 0;
    bottom: -10px;
  }
}

.programs-slider-controller{
  background: #e8e8e8;
  & > .wrapper{
    width: $page-width;
    margin: 0 auto;
  }
}

.divided-title{
  border-bottom: none !important;
  h1{
    font-size: 1.8em !important;
